import React, { useEffect, useRef, useState } from "react";
import { Layer, Group, Line, Rect, Text, Image } from "react-konva";
import bed from "../../assets/icon/room/bed.png";
import door from "../../assets/icon/room/door.png";
import oth from "../../assets/icon/room/oth.png";

const ObjectRenderer = ({ object = [], convertCoords }) => {
  const { type, up_left, low_left, up_right, low_right } = object;

  // Convert points for the object shape
  const objPoints = [
    ...convertCoords(up_left),
    ...convertCoords(low_left),
    ...convertCoords(low_right),
    ...convertCoords(up_right),
  ];

  // Object styling based on type
  const objectStyles = {
    0: { fill: "black", label: "Invalid Area", stroke: "darkred", icon: "oth" },
    1: { fill: "black", label: "Custom Area", stroke: "black", icon: "oth" },
    2: { fill: "black", label: "Bed", stroke: "darkblue", icon: "bed" },
    3: {
      fill: "black",
      label: "Interference Area",
      stroke: "darkorange",
      icon: "oth",
    },
    4: { fill: "black", label: "Door", stroke: "darkgreen", icon: "door" },
    5: {
      fill: "black",
      label: "Monitoring Area",
      stroke: "darkpurple",
      icon: "oth",
    },
  };

  const style = objectStyles[type] || {
    fill: "lightgray",
    label: "Unknown",
    stroke: "gray",
  };

  const textRef = useRef();
  const iconRef = useRef();
  const [textWidth, setTextWidth] = useState(0);
  const [textHeight, setTextHeight] = useState(0);
  const [iconWidth, setIconWidth] = useState(0);
  const [iconHeight, setIconHeight] = useState(0);
  const [iconImage, setIconImage] = useState(null);

  // Set text width and height once the component is mounted
  useEffect(() => {
    if (textRef.current) {
      setTextWidth(textRef.current.getWidth());
      setTextHeight(textRef.current.getHeight());
    }

    // Loading the icon image dynamically
    const img = new window.Image();
    img.src = style.icon === "bed" ? bed : style.icon === "door" ? door : oth;

    img.onload = () => {
      setIconImage(img);
      setIconWidth(40);
      setIconHeight(40);
    };
  }, [style.icon]);

  // Calculate the center of the polygon (bounding box center)
  const calculateCenter = (points) => {
    let minX = Math.min(...points.filter((_, index) => index % 2 === 0));
    let maxX = Math.max(...points.filter((_, index) => index % 2 === 0));
    let minY = Math.min(...points.filter((_, index) => index % 2 !== 0));
    let maxY = Math.max(...points.filter((_, index) => index % 2 !== 0));

    return [(minX + maxX) / 2, (minY + maxY) / 2];
  };

  const [centerX, centerY] = calculateCenter(objPoints);

  return (
    <>
      <Line
        points={objPoints}
        closed
        fill={style.fill}
        // stroke={style.stroke}
        strokeWidth={2}
      />
      {/* Render the icon once the image is loaded */}
      {iconImage && (
        <Image
          ref={iconRef}
          image={iconImage} // Pass the loaded image to the Image component
          x={centerX - 40 / 2} // Center the icon horizontally
          y={centerY - 40 / 2} // Center the icon vertically
          width={40}
          height={40}
        />
      )}
      {/* Render text at the center of the polygon */}
      <Rect
        x={centerX - textWidth / 2 - 5} // Add padding for the background
        y={centerY - (textHeight + 60) / 2 - 5} // Match the text position and add padding
        width={textWidth + 10} // Add horizontal padding
        height={textHeight + 10} // Add vertical padding
        fill="rgba(0, 0, 0, 0.7)" // Background color with transparency
        cornerRadius={4} // Optional: Rounded corners
      />
      <Text
        ref={textRef}
        text={style.label}
        fontSize={14}
        fontStyle="bold"
        fill="white"
        x={centerX - textWidth / 2} // Center the text and icon horizontally
        y={centerY - (textHeight + 60) / 2} // Center the text and icon vertically
      />
    </>
  );
};

export default ObjectRenderer;
